@import reset

*
    margin: 0
    padding: 0
    box-sizing: border-box

a
    text-decoration: none
    &:focus
        outline: none 

html
    scroll-behavior: smooth
    overflow-x: hidden !important

body
    overflow-x: hidden !important
    background: #FFFFFF

section
    overflow: hidden


=Bold
    font-family: 'Montserrat', sans-serif
    font-weight: 700
    color: #fff
=SemiBold
    font-family: 'Montserrat', sans-serif
    font-weight: 600
    color: #fff
=Normal
    font-family: 'Montserrat', sans-serif
    font-weight: 400
    color: #fff

$Brown: #EDC5AC
$Brown2: #D7A484
$Brown3: #B88E6D
$BrownDark: #8C5A3C
$White: #ffffff
$Gray: #464646
$Black: #000000

    
.wrapper
    max-width: 1200px
    padding: 0 20px
    margin: 0 auto

// Header start
.main__sec
    padding-top: 25px
    max-height: 100vh
    overflow: hidden
    position: relative
    margin-bottom: 140px
    .video
        max-height: 100vh
        overflow: hidden
        margin-top: -25px
        &::after
            content: ''
            position: absolute
            display: block
            top: 0
            left: 0
            z-index: 2
            width: 100%
            height: 100%
            background-color: rgb(0, 0, 0, .4)
        .video__media
            position: relative
            z-index: -1
            padding-bottom: 56%
            video
                width: 100%
                height: auto
                position: absolute
                top: 0
                left: 0
        .content__center
            position: absolute
            top: 40%
            left: 38%
            z-index: 99
            .text
                display: flex
                flex-direction: column
                align-items: center
                .stars
                    display: flex
                    align-items: center
                    div
                        margin-right: 20px
                        &:last-child
                            margin-right: 0
                .title
                    @include Bold
                    font-weight: 900
                    font-size: 48px
                    letter-spacing: 0.1em
                    margin: 15px 0
                .desc
                    @include Normal
                    font-weight: 900
                    margin-bottom: 20px
                    letter-spacing: 0.3em
                .desc__2
                    display: flex
                    justify-content: space-between
                    align-items: center
                    letter-spacing: 0.02em
                    p
                        @include SemiBold
                        font-weight: 500
                    .circle
                        width: 4px
                        height: 4px
                        border-radius: 50%
                        background: #fff
                        margin: 0 12px
            .play__btn
                position: relative
                margin-top: 60px
                height: 105px
                border-radius: 50%
                display: flex
                justify-content: center
                align-items: center
                .img
                    position: absolute
                    width: 40px
                    height: 40px
                    background: url(../Photos/Icons/Play.png)
                    background-size: cover
                    border-radius: 50%
                    z-index: 10
                    &:hover
                        cursor: pointer
                .circle__text__video
                    position: absolute
                    width: 100%
                    height: 89%
                    animation: video__btn 10s linear infinite
                    p
                        display: none
                        span
                            @include SemiBold
                            margin-right: 10px
                            position: absolute
                            left: 50%
                            font-size: 16px
                            transform-origin: 0 46px
    

        .content__right
            position: absolute
            right: -79px
            top: 40%
            z-index: 50
            .btns
                display: flex
                flex-direction: column
                .button
                    display: flex
                    align-items: center
                    transform: rotate(90deg)
                    background: $Brown3
                    padding: 20px
                    max-height: 38px
                    transition: .2s ease all
                    svg
                        transform: rotate(-90deg)
                        margin-right: 15px
                        transition: .2s ease all
                    a
                        button
                            @include SemiBold
                            font-size: 14px
                            color: $White
                            border: 0
                            background: 0
                            transition: .2s ease all
                    &:hover
                        cursor: pointer
                        background: $White
                        svg
                            path
                                fill: $Brown3
                        a
                            button
                                color: $Brown3
                    &:first-child
                        margin-bottom: 140px
    header
        position: fixed
        padding: 25px 0
        top: 0
        left: 0
        width: 100%
        z-index: 100
        transition: .2s ease all
        &.header__active
            background: $White
            .navigation__menu
                .logo
                    h1
                        color: $Brown3
                nav
                    ul
                        li
                            a
                                color: $Gray
                            .arrow__header
                                path
                                    fill: $Gray
        .navigation__menu
            display: flex
            align-items: center
            justify-content: space-between
            .logo
                z-index: 1000
                h1
                    @include Bold
                    font-size: 16px
                    padding: 25px 16px
                    border: 2px solid #fff
                    transition: .2s ease all

            nav
                z-index: 100
                ul
                    display: flex
                    align-items: center
                    justify-content: space-between
                    li
                        position: relative
                        margin-right: 30px
                        z-index: 100
                        padding: 10px
                        svg
                            margin-left: 15px
                            transition: .2s ease all
                        &.active__li
                            background: $White
                            border-radius: 7px 7px 0 0
                            box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2)
                            svg
                                transform: rotate(180deg)
                                path
                                    fill: $Brown3 !important
                        a
                            @include SemiBold
                            color: #fff
                            font-size: 13px
                            transition: .2s ease all
                            letter-spacing: 0.01em
                            &::after
                                content: ""
                                display: block
                                position: absolute
                                bottom: -4px
                                left: 0
                                width: 0
                                height: 2px
                                background-color: $Brown3
                                transition: .4s ease all
                            &:hover:after
                                width: 100%
                            &.title__active
                                color: $Brown3
                            
                        .dop__menu
                            display: none
                            transition: .2s ease all
                            &.menu__active
                                display: flex
                                flex-direction: column
                                align-items: flex-start
                                position: absolute
                                top: 36px
                                padding: 20px
                                z-index: 100
                                background: $White
                                left: 0
                                border-radius: 0 0 7px 7px
                                transition: .2s ease all
                                width: 100%
                                box-shadow: 0px 10px 10px 2px rgba(34, 60, 80, 0.2)
                                a
                                    color: $Gray
                                    margin-bottom: 5px
                                    &:hover
                                        color: $Brown3
                        &:hover
                            cursor: pointer
                            svg
                                path
                                    fill: $Brown3
                            a
                                color: $Brown3
                                
                            
            a
                z-index: 100
                button
                    @include SemiBold
                    border: 0
                    background: $Brown3
                    padding: 10px 45px
                    font-size: 14px
                    transition: .2s ease all
                    border: 2px solid $Brown3
                    &:hover
                        background: $White
                        color: $Brown3

#detach-button-host
    display: none !important
    

@keyframes video__btn 
    0%
        transform: rotate(-360deg)
    100%
        transform: rotate(0deg)


    
// Header end


// Hotel Medispa Section start

.hotel__medispa__sec
    margin-bottom: 170px
    .two__blocks
        display: flex
        column-gap: 100px
        .first__block
            display: flex
            flex-direction: column
            .stars
                display: flex
                align-items: center
                margin-bottom: 25px
                div
                    margin-right: 10px
                    &:last-child
                        margin-right: 0
                    svg
                        path
                            fill: $Brown3
            .title
                @include Bold
                font-size: 50px
                line-height: 70px
                margin-bottom: 40px
                color: $Gray
            .desc
                @include Normal
                line-height: 35px
                font-size: 16px
                color: $Gray
                span
                    font-weight: 600
    .advantages
        background-color: #fff
        margin-top: -270px
        display: flex
        .block
            display: flex
            padding: 30px 10px
            flex-direction: column
            align-items: center
            background-color: #fff
            &:last-child
                padding-right: 30px
            .desc
                @include SemiBold
                margin-top: 10px
                font-size: 12px
                text-align: center
                max-width: 160px
                line-height: 20px
                color: $Brown3

// Hotel Medispa Section end



// Combination Section start

.combination__sec
    background: #F5E4D9
    padding: 70px 0 40px
    margin-bottom: 100px
    overflow: visible
    .top__info
        display: flex
        margin-bottom: 90px
        img
            margin-top: -150px
            margin-right: 80px
        .info
            max-width: 720px
            .title
                @include Bold
                font-size: 40px
                line-height: 60px
                color: $Gray
                margin-bottom: 25px
            .desc
                @include Normal
                font-size: 16px
                line-height: 38px
                color: $Gray
    .cards__blocks
        display: flex
        justify-content: space-between
        column-gap: 40px
        .cards__block
            .title
                display: flex
                align-items: center
                @include Bold
                font-size: 28px
                color: $Gray
                margin-bottom: 40px
                .circle
                    width: 10px
                    height: 10px
                    background: $BrownDark
                    border-radius: 50%
                    margin-right: 10px
            .cards
                display: flex
                flex-direction: column
                row-gap: 40px
                .card
                    display: flex
                    flex-direction: column
                    .photo
                        position: relative
                        .desc
                            position: absolute
                            left: 30px
                            bottom: 40px
                            @include Bold
                            font-size: 28px
                    .content__card
                        padding: 20px 30px
                        background-color: #fff
                        .days
                            .title
                                @include SemiBold
                                font-size: 13px
                                margin-bottom: 20px
                                color: $Gray
                            .buttons
                                display: flex
                                justify-content: space-between
                                button
                                    background: 0
                                    border: 2px solid $Brown3
                                    color: $Brown3
                                    padding: 10px 15px
                                    font-size: 13px
                                    transition: .2s ease all
                                    &:hover
                                        color: $White
                                        background: $Brown3
                        .desc
                            @include Normal
                            font-size: 14px
                            margin-top: 30px
                            line-height: 20px
                            margin-bottom: 40px
                            max-width: 300px
                            color: $Gray
                        .dop__desc
                            display: none
                            @include Normal
                            font-size: 14px
                            margin-top: 30px
                            line-height: 20px
                            margin-bottom: 40px
                            max-width: 300px
                            color: $Gray
                        .btn__dop
                            display: none
                            @include SemiBold
                            background: $Brown3
                            width: 100%
                            padding: 10px 20px
                            color: $White
                            border: 0
                            margin-bottom: 40px
                            transition: .2s ease all
                            border: 2px solid $Brown3
                            &:hover
                                background: $White
                                color: $Brown3
                        .more__info
                            display: inline-flex
                            align-items: center
                            &:hover
                                cursor: pointer
                            svg
                                transition: .2s ease all
                                path
                                    fill: $Brown3
                            p
                                @include Normal
                                font-size: 14px
                                margin-left: 10px
                                color: $Gray
                        &.content__active
                            .dop__desc
                                display: block
                            .btn__dop
                                display: block
                            .more__info
                                svg
                                    transform: rotate(-180deg)
    
// Combination Section end


// Services Section start

.services__sec
    .title__sec
        @include Bold
        font-size: 50px
        margin-bottom: 70px
        margin-left: 40px
        color: $Gray
    .blocks
        .block
            margin-bottom: 50px
            padding: 40px 30px
            background: #F5E4D9
            .info
                margin-bottom: 50px
                .title
                    @include Bold
                    font-size: 28px
                    margin-top: -55px
                    margin-bottom: 20px
                    color: $Gray
                .text
                    display: flex
                    align-items: center
                    img
                        margin-right: 20px
                    h1
                        @include Bold
                        font-size: 28px
                        color: $Gray
                        line-height: 40px
                    ul
                        li
                            @include Normal
                            font-size: 18px
                            line-height: 34px
                            color: $Gray
                            &:before
                                content: ''
                                display: inline-block
                                height: 4px
                                width: 4px
                                vertical-align: middle
                                border-radius: 50%
                                background-color: $Gray
                                margin-right: 10px
                                margin-top: -3px
                
            .cards, .mini__cards
                display: grid
                grid-template-columns: repeat(3, 530px)
                gap: 40px
                .card
                    position: relative
                    &:first-child
                        grid-column-start: 1 
                        grid-column-end: 3
                    
                    &:nth-child(2)
                        grid-column-start: 1 
                        grid-column-end: 2
                    img
                        width: 100%
                    .desc
                        position: absolute
                        left: 30px
                        bottom: 30px
                        .title
                            @include Bold
                            font-size: 20px
                            margin-bottom: 10px
                        .text
                            @include Normal
                            margin-bottom: 10px
                        ul
                            list-style-type: disc
                            li
                                @include Normal
                                font-size: 16px
                                line-height: 30px
                                color: $White
                                margin-left: 25px

            .mini__cards
                margin-top: 40px
                display: grid
                grid-template-columns: repeat(3, 340px)
                .card
                    &:first-child
                        grid-column-start: 1 
                        grid-column-end: 1
                    
                    &:nth-child(2)
                        grid-column-start: 2
                        grid-column-end: 2
                                

// Services Section end


// Gallery Section start

.gallery__sec
    position: relative
    padding-bottom: 70px
    margin-bottom: 100px
    .title__sec
        @include Bold
        margin-top: 50px
        font-size: 50px
        color: $Gray
        margin-bottom: 35px
    .btns__sections
        display: flex
        justify-content: space-between
        align-items: center
        margin-bottom: 50px
        .button
            position: relative
            @include SemiBold
            font-size: 16px
            background: 0
            border: 0
            color: $Gray
            transition: .2s ease all
            margin-right: 5px
            &:last-child
                margin-right: 0
            &:hover
                cursor: pointer
                color: $Brown3
            &::after
                content: ""
                display: block
                position: absolute
                left: 0
                width: 0
                height: 2px
                background-color: $Brown3
                transition: .4s ease all
            &:hover:after, &:focus:after 
                width: 100%
            &.active__tab
                color: $Brown3
                &::after
                    content: ""
                    display: block
                    position: absolute
                    left: 0
                    width: 100%
                    height: 2px
                    background-color: $Brown3
                    transition: .4s ease all 
    .content__tabs
        display: none
        position: relative
        .swiper
            margin-left: -500px
        .swiper-slide-active 
            margin-right: 600px !important
            .slide 
                transform: scale(1) !important
                transition: transform .2s ease
        .swiper-slide 
            .slide 
                transform: scale(0.7)
                transition: transform .2s ease
        .swiper-button-next, .swiper-button-prev
            width: 60px
            right: -60px
        .swiper-button-next::after
            content: ''
            display: block
            width: 60px
            height: 60px
            background: url(../Photos/Blocks/Gallery/Arrow__next.png) no-repeat
        .swiper-button-prev
            left: 0px
            top: 90px
        .swiper-button-prev::after
            content: ''
            display: block
            width: 60px
            height: 60px
            background: url(../Photos/Blocks/Gallery/Arrow__prev.png) no-repeat
        
        .swiper__btns
            display: flex
            flex-direction: column
            position: absolute
            bottom: 150px
            right: 13.3%
        &.active__content
            display: block

    .bg__1
        position: absolute
        top: 0
        left: -50px
        width: 340px
        height: 770px
        background: rgba(245, 228, 217, 0.7)
        z-index: -1
    .bg__2
        position: absolute
        top: 0
        right: -50px
        width: 340px
        height: 770px
        background: rgba(245, 228, 217, 0.7)
        z-index: -1

// Gallery Section end

// Rooms Section start

.rooms__sec
    overflow: visible
    background: rgba(245, 228, 217, 0.7)
    .top__info
        display: flex
        column-gap: 100px
        margin-bottom: 130px
        .text
            margin-top: 50px
            .title
                @include Bold
                font-size: 50px
                color: $Gray
                margin-bottom: 40px
            .desc
                @include Normal
                font-size: 18px
                max-width: 820px
                color: $Gray
                line-height: 40px
        img
            margin-top: -80px
    .rooms__cards
        display: flex
        flex-wrap: wrap
        gap: 40px
        .card__dop
            display: none !important
        .card
            display: flex
            flex-direction: column
            img
                width: 100%
                object-fit: cover
            .content
                display: flex
                flex-direction: column
                padding: 30px
                background: $White
                .title
                    @include Bold
                    font-size: 20px
                    margin-bottom: 10px
                    color: $Gray
                .desc
                    @include Normal
                    color: $Gray
                    line-height: 30px
                    font-size: 16px
                    max-width: 500px
                    margin-bottom: 40px
                .btns
                    display: flex
                    justify-content: space-between
                    button
                        display: flex
                        align-items: center
                        background: 0
                        border: 0
                        .svg
                            padding: 10px
                            width: 40px
                            height: 40px
                            background: $Brown3
                        p
                            @include Bold
                            font-size: 13px
                            margin-left: 10px
                            color: $Black
                            sup
                                font-size: 10px
                                vertical-align: super
    .btn__show__cards
        display: flex
        justify-content: center
        align-items: center
        margin-top: 60px
        padding-bottom: 60px
        button
            display: flex
            align-items: center
            background: 0
            border: 0
            background: 0
            border: 0
        .show__title
            @include Normal
            color: $Gray
            font-size: 18px
            margin-right: 20px
            transition: .2s ease all
            &:hover
                color: $Brown3
                cursor: pointer
        .svg
            padding: 10px
            width: 40px
            height: 40px
            background: $Brown3
            svg
                transition: .2s ease all
                &.active__show__arrow
                    transform: rotate(-180deg)           

// Rooms Section end



// Questions Section start

.questions__sec
    .title__sec
        @include Bold
        font-size: 50px
        margin: 100px 0 50px
        color: $Gray
    .accordions__blocks
        display: grid
        grid-template-columns: repeat(2, 50%) 
        column-gap: 40px
    .accordions
        display: flex
        flex-direction: column
        gap: 40px
        .accordion
            border-top: 1px solid $Brown3
            padding: 20px 0 0
            .title
                display: flex
                justify-content: space-between
                align-items: center
                p
                    @include Normal
                    font-size: 18px
                    color: $Gray
                    line-height: 40px
                button
                    border: 0
                    padding: 10px
                    width: 40px
                    height: 40px
                    background: $Brown3
            .content
                max-height: 0
                margin-top: 20px
                overflow: hidden
                border-bottom: 1px solid $Brown3
                transition: max-height 0.2s ease-out
                p
                    @include Normal
                    font-size: 16px
                    color: $Gray
                    max-width: 520px
                    line-height: 25px
                    padding-bottom: 20px


// Questions Section end


// News Section start

.news__sec
    position: relative
    margin-top: 100px
    padding-bottom: 70px
    .title__sec
        @include Bold
        font-size: 50px
        margin: 50px 0
        color: $Gray
    .card
        display: flex
        flex-direction: column
        img
            width: 100%
            object-fit: cover
        .content
            display: flex
            flex-direction: column
            row-gap: 10px
            padding: 20px
            background: $White
            .data
                @include Normal
                color: #BBBBBB
                font-size: 16px
            .title
                @include Bold
                font-size: 20px
                color: $Gray
            .desc
                @include Normal
                font-size: 16px
                line-height: 30px
                color: $Gray
            .btn
                a
                    @include Normal
                    font-size: 14px
                    border: 0
                    background: 0
                    color: $Brown3
    .swiper-button-next, .swiper-button-prev
        width: 60px
        right: -100px
    .swiper-button-next::after
        content: ''
        display: block
        width: 60px
        height: 60px
        background: url(../Photos/Blocks/Gallery/Arrow__next.png) no-repeat
    .swiper-button-prev
        left: -60px
        top: 0px
    .swiper-button-prev::after
        content: ''
        display: block
        width: 60px
        height: 60px
        background: url(../Photos/Blocks/Gallery/Arrow__prev.png) no-repeat
    
    .swiper__btns
        display: flex
        position: absolute
        top: 80px
        right: 13.3%
    .bg
        position: absolute
        width: 100%
        height: 70%
        bottom: 0
        background: rgba(245, 228, 217, 0.7)

// News Section end



// Achivments Section and About us Section start

.achievments__sec, .about__use__sec
    margin-bottom: 50px
    .title__sec
        @include Bold
        font-size: 50px
        margin: 50px 0
        color: $Gray
    .card
        .line__photo
            display: flex
            justify-content: space-between
            align-items: center
            .line
                display: block
                width: 100%
                max-width: 200px
                height: 2px
                background: $Brown3
        ul
            li
                @include Normal
                font-size: 20px
                line-height: 34px
                color: $Gray
                &:before
                    content: ''
                    display: inline-block
                    height: 6px
                    width: 6px
                    vertical-align: middle
                    border-radius: 50%
                    background-color: $Brown3
                    margin-right: 10px
                    margin-top: -3px
        .line__bottom
            display: block
            width: 100%
            margin: 20px 0
            height: 2px
            background: $Brown3
        .title
            @include Bold
            color: $Gray
            font-size: 28px
            margin-bottom: 10px
        .desc
            @include Normal
            font-size: 16px
            color: $Gray
            line-height: 30px
            margin-bottom: 10px
        .btn
            a
                @include Bold
                color: $Brown3
                font-size: 20px
                


    .swiper-button-next, .swiper-button-prev
        width: 60px
        right: -100px
    .swiper-button-next::after
        content: ''
        display: block
        width: 60px
        height: 60px
        background: url(../Photos/Blocks/Gallery/Arrow__next.png) no-repeat
    .swiper-button-prev
        left: -60px
        top: 0px
    .swiper-button-prev::after
        content: ''
        display: block
        width: 60px
        height: 60px
        background: url(../Photos/Blocks/Gallery/Arrow__prev.png) no-repeat
    
    .swiper__btns
        display: flex
        position: absolute
        top: 80px
        right: 9%



.about__use__sec
    .swiper-slide
        &:hover
            cursor: -webkit-image-set(url(../Photos/Icons/cursor.svg) 1x),move

// Achivments Section and About us Section end


// Reviews Section start

.reviews__sec
    position: relative
    padding-bottom: 80px
    margin-bottom: 140px
    .title__sec
        @include Bold
        font-size: 50px
        margin: 50px 0
        color: $Gray
    .card
        img
            width: 100%
        .content
            padding: 20px
            background: $White
            .title
                @include Bold
                color: $Gray
                font-size: 20px
                margin: 0 0 10px
            .desc
                @include Normal
                font-size: 16px
                color: $Gray
                line-height: 30px
                margin-bottom: 15px
            .btn
                a
                    @include Normal
                    font-size: 14px
                    color: $Brown3
                


    .swiper-button-next, .swiper-button-prev
